.movies__movie {
  background-color: #949398;
  display: flex;
}

.movies__movie img {
  align-self: center;
  width: 100%;
  height: 100%;
}

.movie__data {
  color: #f4df4e;
  padding: 0 10px;
  display: grid;
  grid-template-columns: 1fr 4fr;
  grid-template-rows: 2fr 1fr 4fr;
}

.movie__title {
  grid-column: 1 / 3;
  width: 100%;
  font-size: 24px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.movie__year {
  opacity: 0.5;
  font-size: 12px;
  font-weight: 300;
}

.movie__genres {
  opacity: 0.5;
  list-style: none;
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  font-size: 12px;
}

.genres__genre {
  width: 80px;
}

.movie__summary {
  grid-column: 1 / 3;
  grid-row: 3 / 4;
  font-size: 13px;
  letter-spacing: 1px;
  line-height: 2em;
  text-align: justify;
}
